        <div class="user-actver"><img class="actver" src="https://qdmossmall.qdama.cn/mallH5/iFeGTFtNPDjDKJCsBeGB2pB76APGQW6G.jpg" alt=""></div>

    </div>
    <div class="serach-info" >
        <van-tabs v-model="active" @change="typeChange" >
            <van-tab title="全部"></van-tab>
            <van-tab title="猛人"></van-tab>
            <van-tab title="办法"></van-tab>
            <van-tab title="机会"></van-tab>
        </van-tabs>
    </div>

</div>
</template>

<script>
import Vue from 'vue';
import { Tab, Tabs,Toast } from 'vant';
Vue.use(Toast);
Vue.use(Tab);
Vue.use(Tabs);
export default {
components: {},
data() {
    return {
        active:0,
        isSearch:false,
        keyWord:'',
        searchData:[],
        total:0,
        form:{
            keyword:'',
            page_size:1000,
            type:null,
        }
    };
},
computed: {},
watch: {},
filters:{
    entityType:function(type){
        let name = '';
        switch(type){
            case 'expertise':
                 name = '猛人';
                 break;
            case 'method':
                 name = '办法';
                 break;
            case 'demand':
                 name = '需求';
                 break; 
             default :
                name = "其他";
                break;                   
        }
        return name;
    }
},
methods: {
    typeChange(val){
        console.log(val);
        if(val===0){
            this.form.type=null;
        }else if(val===1){
            this.form.type='awesome';
        }else if(val===2){
            this.form.type='method';
        }else{
            this.form.type='demand';
        }
        this.handelSearch();
    },
    handelSearch(){
        this.isSearch = true;
        Toast.loading({
            message: '加载中...',
            forbidClick: true,
            loadingType: 'spinner',
        });
    }
},
created() {

},
mounted() {

},
beforeCreate() {},
beforeMount() {},
beforeUpdate() {},
updated() {},
beforeDestroy() {},
destroyed() {},
activated() {},
}
</script>
